.header {
  position: sticky;
  display: flex;

  overflow: hidden;
}
path {
  fill: #fff0f3;
}
body {
  /* Change in CSS */
  background-color: #c9184a;
  color: #fff0f3;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.header-child {
  padding: 10px 20px;
}
.header-child:hover {
  transform: scale(1.1);
  cursor: pointer;
  filter: brightness(0.5);
}

.EverySecond {
  min-height: 36px;
  height: fit-content;

  margin-top: -40px;
  font-size: 25px;
}
h3.TextForEverySecond {
  margin-top: 0%;
  margin-bottom: 0%;
}

@keyframes FrontAnimation {
  0% {
    background-color: red;
    left: 0px;
    top: 0px;
    border-radius: 25%;
  }
  25% {
    background-color: yellow;
    left: 100px;
    top: 0px;
    border-radius: 50%;
  }
  50% {
    background-color: blue;
    left: 100px;
    top: 100px;
    border-radius: 75%;
  }
  75% {
    background-color: green;
    left: 0px;
    top: 100px;
    border-radius: 50%;
  }
  100% {
    background-color: red;
    left: 0px;
    top: 0px;
    border-radius: 25%;
  }
}
.flyingobject {
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: red;
  animation-name: FrontAnimation;
  animation-duration: 5s;
  animation-iteration-count: infinite;
}
.flyingobject.a {
  animation-delay: 3s;
}
.flyingobject.b {
  animation-delay: 1s;
}

.flyingobject.c {
  animation-delay: 2s;
}

.flyingobject.d {
  animation-delay: 3s;
}
.background {
  position: absolute;
  width: 100%;
  height: 60%;

  z-index: -1;
  overflow: hidden;
}
