  margin: auto;
  padding: 3%;
  border-radius: 25px;
  margin-bottom: 20px;
}

.header-phone {
  display: none;
  z-index: 1;
}
.header-phone-menu {
  position: fixed;
  width: 50%;
  height: 100%;
  z-index: 1;
}

.header-phone-menu > * {
  font-size: 30px;
  margin: 20px;
}
.header-phone {
  height: 60px;
}
.phone-menu-button {
  width: 20%;
  outline: none;
  cursor: pointer;
  border-style: solid;
}
.phone-menu-button > * {
  height: 5px;
  width: 100%;

  margin-bottom: 7px;
  margin-top: 7px;
}
svg.left-arrow > * {
  transform: scale(2.6);
  height: 100px;
  width: 100px;
  fill: white;
}

@media only screen and (max-width: 900px) {
  .header-phone-menu {
    display: none;
  }

  #Front {
    flex-direction: column;
  }
  #Front > * {
    margin: auto;
  }
  .header {
    display: none;
  }

  .header-phone {
    display: flex;
  }
  .header-child {
    margin-bottom: 0%;
    font-size: 18px;
  }
  .ColorChanger {
    width: auto;
  }
  .ColorChangerHolder {
    margin: 20px 0px;
    margin-top: 20px;
  }
  .ColorChangerHolder > .ColorTheme.center {
    font-size: 14px;
  }
  .ColorTheme {
    width: 100%;
    margin-top: 10px;
  }

  .ColorTheme > * {
    display: inline-block;
    height: 100%;
    width: 15%;
  }

  p.Description {
    font-size: 2vw;
  }

  .MetaBox-online {
    font-size: 1.8vw;
  }

  .greybox {
    font-size: 1.3vw;
  }

  .greybox h4 {
    font-size: 2.5vw;
    text-decoration: underline;
  }
  .project-list-ul {
    flex-wrap: wrap;
  }

  .header-phone-menu {
    animation-name: headermenuF;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
  }

  @keyframes headermenuF {
    from {
      left: -500px;
    }
    to {
      left: 0px;
    }
  }

  @keyframes headermenuB {
    from {
      left: 0px;
    }
    to {
      left: -500px;
    }
  }

  .TextForEverySecond {
    font-size: 2.3vw;
  }
}
